<template>
  <div>
    <div class="cnt">
      <el-row>
        <el-col :span="12">
          <el-input placeholder="输入三字码/拼音/城市搜索" @keyup.native="onChange" v-model="keyword"></el-input>
        </el-col>
        <el-col :span="1">&nbsp;</el-col>
        <el-col :span="2">
          <el-select v-model="type" placeholder="请选择" @change="onChange">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="6">
          <el-button @click="onDownAll" type="primary">下载查询结果(JSON)</el-button>
          <el-button @click="onReset" type="danger">重置</el-button>
        </el-col>
      </el-row>
      <el-table border stripe :data="tableData" style="width: 100%;margin-bottom:20px;margin-top: 20px" v-loading="loading">
        <el-table-column prop="code" label="三字码"></el-table-column>
        <el-table-column prop="city" label="城市名"></el-table-column>
        <el-table-column prop="name" label="机场名字"></el-table-column>
        <el-table-column prop="pinyin" label="拼音"></el-table-column>
        <el-table-column
          prop="intl"
          label="国际/国内"
          :formatter="(row) => { return row.intl ? '国际' : '国内' }"
        ></el-table-column>
      </el-table>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="page"
        :page-sizes="[10,20,30,50]"
        :page-size="limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        style="margin-bottom: 20px;"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import cities from '@/data/city'
import _ from 'lodash'
export default {
  name: 'searchPage',
  data () {
    return {
      allTableData: [],
      keyword: '',
      tableData: [],
      findTableData: [],
      page: 0,
      limit: 0,
      total: 0,
      loading: false,
      type: 0,
      options: [{
        label: '全部',
        value: 0
      }, {
        label: '国内',
        value: 1
      }, {
        label: '国际',
        value: 2
      }]
    }
  },
  methods: {
    init () {
      this.allTableData = cities
      this.page = 1
      this.limit = 10
      this.renderTable()
    },
    export (content) {
      // 创建隐藏的可下载链接
      var eleLink = document.createElement('a')
      eleLink.download = 'data.js'
      eleLink.style.display = 'none'
      // 字符内容转变成blob地址
      var blob = new Blob([JSON.stringify(content)])
      eleLink.href = URL.createObjectURL(blob)
      // 触发点击
      document.body.appendChild(eleLink)
      eleLink.click()
      // 然后移除
      document.body.removeChild(eleLink)
    },
    renderTable (page = 1) {
      this.loading = true
      let keyword = this.keyword
      let type = this.type
      setTimeout(() => {
        // 先筛选国内国外
        let findData = this.allTableData.filter(ele => {
          if (type === 0) {
            return true
          } else if (type === 1) {
            return !ele.intl
          } else if (type === 2) {
            return ele.intl
          }
        })
        // 再筛选关键字
        findData = findData.filter(ele => {
          return ele.code.indexOf(keyword.toUpperCase()) > -1 ||
              ele.name.indexOf(keyword) > -1 ||
              ele.city.indexOf(keyword) > -1 ||
              ele.pinyin.indexOf(keyword) > -1
        })
        this.findTableData = findData
        this.tableData = findData.slice(0 + (this.page - 1) * this.limit, this.limit + (this.page - 1) * this.limit)
        this.total = findData.length
        this.loading = false
      }, 300)
    },
    handleSizeChange (size) {
      this.page = 1
      this.limit = size
      this.renderTable()
    },
    handleCurrentChange (page) {
      this.page = page
      this.renderTable()
    },
    onDownAll () {
      this.export(this.findTableData)
    },
    onReset () {
      this.page = 1
      this.limit = 10
      this.keyword = ''
      this.type = 0
      this.renderTable()
    },
    onChange () {
      let _inner = () => {
        this.page = 1
        this.limit = 10
        this.renderTable()
      }
      _.debounce(_inner, 500)()
    }
  },
  mounted () {
    this.init()
  }
}
</script>

<style lang="less" scoped>
.cnt{
  width: 100%;
}
</style>



